  background-image: url("fon.4c9be166.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  margin: 0;
  overflow: hidden;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.board {
  width: 100vw;
