  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  border-radius: 10px;
  outline: none;
  font-size: 25px;
  font-weight: bold;
  transition: all .5s;
  position: absolute;
  top: 55%;
  left: 38%;
}

#rocket {
  color: green;
  font-size: 42px;
  position: fixed;
  top: 6%;
}

.bounce-top {
  animation: bounce-top .9s infinite both;
}

.rotate-90-cw {
  animation: rotate-90-cw .4s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes rotate-90-cw {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    transform: rotate(180deg);
  }
}

@keyframes bounce-top {
  0% {
    opacity: 1;
    animation-timing-function: ease-in;
    transform: translateY(-45px);
  }

  24% {
    opacity: 1;
  }

  40% {
    animation-timing-function: ease-in;
    transform: translateY(-24px);
  }

  65% {
    animation-timing-function: ease-in;
    transform: translateY(-12px);
  }

  82% {
    animation-timing-function: ease-in;
    transform: translateY(-6px);
  }

  93% {
    animation-timing-function: ease-in;
    transform: translateY(-4px);
  }

  25%, 55%, 75%, 87% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  100% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: translateY(0);
  }
}

.span {
  width: 40%;
  height: 40%;
  border-radius: 50%;
  display: block;
  position: absolute;
  box-shadow: 2px 2px 3px #0006;
}

.one {
  background: #f3b222;
  animation-delay: 1.5s;
}

.two {
  background: #f0653e;
  animation-delay: 1s;
}

.three {
  background: #b9c0c0;
  animation-delay: .5s;
}

.four {
  background: #8f7473;
}

@keyframes run {
  0% {
    border-radius: 50%;
    transform: translate(0%);
  }

  25% {
    border-radius: 0%;
    transform: translate(150%)scale(.5);
  }

  50% {
    border-radius: 50%;
    transform: translate(150%, 150%);
  }

  75% {
    border-radius: 0%;
    transform: translate(0%, 150%)scale(.5);
  }
}

.end {
  display: none;
}

.the-end {
  text-align: center;
  color: green;
  font-family: cursive;
  font-size: 50px;
}

.text-focus-in {
  animation: text-focus-in 1s cubic-bezier(.55, .085, .68, .53) both;
}

@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
  }
}

